import Vue from 'vue'
import Router from 'vue-router'

import Home from './views/Home.vue'
import HttpsGen from './views/HttpsGen.vue'
import About from './views/About.vue'

Vue.use(Router)

export default new Router({ 
  linkExactActiveClass: 'active',
    routes: [{ 
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/httpsgen',
      name: 'httpsgen',
      component: HttpsGen,
    },
    {
      path:'/about',
      name: 'about',
      component: About
    }
  ]

   
})