<template>
  <b-overlay :show="show" rounded="sm" @shown="onShown" @hidden="onHidden">

  <b-card title="Certificate Signing Request" header-tag="header" footer-tag="footer">
            
            <b-input-group>
                <template v-slot:prepend>
                    <b-input-group-text >Domain:</b-input-group-text>
                </template>
                <b-form-input v-model="domain" type="text" :state="dmnState"  placeholder="yourDomain.tld"  size="lg"></b-form-input>

                <template v-slot:append>
                    <b-dropdown text="Recent" variant="outline-info">
                    <b-dropdown-item>-None-</b-dropdown-item>
                    </b-dropdown>
                    <b-button href="#"  @click="getCsr" :disabled="dmnValidated" variant="success" size="lg">Generate CSR</b-button>
                </template>
            </b-input-group>
            <b-card-text> 
              <b-button-group size="sm">
                  <b-button variant="primary">Auto Generate</b-button>
                  <b-button variant="success">HTTPSGen Server</b-button>
                  <b-button variant="warning">Clear</b-button>
              </b-button-group>
              <b-form-textarea
                id="pub-key"
                v-model="csr"
                placeholder=""
                rows="8"
                max-rows="8" 
                
              ></b-form-textarea>
            

              <div class="mt-2 help-content">
                How to generate a new Certificate Signing Request (CSR):<br>
                <ol>
                    <li>
                        Generate a TLS private key if you don't have one:<br>
                        (KEEP DOMAIN.KEY SECRET!)<br>
                        <code>openssl genrsa 4096 &gt; domain.key</code>
                    </li>
                    <li>
                        Generate a CSR for your the domains you want certs for:<br>
                        (replace "foo.com" with your domain)<br>
                        Linux:
            <code>    <pre>
            #change "/etc/ssl/openssl.cnf" as needed:
            #  Debian: /etc/ssl/openssl.cnf
            #  RHEL and CentOS: /etc/pki/tls/openssl.cnf
            #  Mac OSX: /System/Library/OpenSSL/openssl.cnf
            </pre>
            openssl req -new -sha256 -key domain.key -subj "/"  -reqexts SAN -config &lt;(cat /etc/ssl/openssl.cnf   &lt;(printf "\n[SAN]\nsubjectAltName=DNS:foo.com,DNS:www.foo.com"))
            </code>
                    </li>
                    <li>
                        Copy and paste the CSR into the box above.<br>
                    </li>
                </ol>
            </div>
            </b-card-text>
            
            
            <template v-slot:footer>
              <b-container>
                <b-row>
                  <b-col sm="8"> Validate CSR  here  </b-col>
                  <b-col sm="4"> <b-button href="#" @click="validatecsr" :disabled="dmnValidated" variant="primary">Validate CSR</b-button></b-col>
                </b-row>
              </b-container>
            </template>
          </b-card>


    <template v-slot:overlay>
      <div class="text-center">
        <b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
        <p id="cancel-label">Generating Public Key or CSR might take a while. Please sit tight...</p>
        <b-button
          ref="cancel"
          variant="outline-danger"
          size="sm"
          aria-describedby="cancel-label"
          @click="show = false"
        >
          Cancel
        </b-button>
      </div>
    </template>  
  </b-overlay>
</template>

<script>

export default { 
  props: { 
    show: { type: Boolean, default: false },
    domain: String,
    loc: String,
  },
  data() { 
    return {
      dmnState: false,
      csr: '',
      dmnValidated:false,
    }
  },
  watch: { 
    domain: { 
      immediate: true,
      handler() { 
        //this.getCsr()
      }
    },
  },
  methods: { 
    validatecsr: function() {  
      if(this.domain === "") return
        fetch('https://165.22.35.93:3030/letsencrypt/validateCsr', { 
        method: 'post',
        body: JSON.stringify({
          loc: this.loc,  
          csr: this.csr,
          domain : this.domain,  
        }) 
      }).then((res) => (res.json()))
          .then((res) => { 
              this.csr = res.msg
              this.dmnValidated = res.success
          })
        
    },
    getCsr:function() { 
      this.showProcessing = true
      fetch('https://165.22.35.93:3030/letsencrypt/getCsr', { 
        method: 'post',
        body: JSON.stringify({
          loc: this.loc,  
          domain : this.domain,  
        }) 
      }).then((res) => (res.json()))
          .then((res) => { 
              this.csr = res.msg
              this.dmnState = res.success?true:false                
              this.showProcessing = false
          })
    },

       makeSimpleToast(variant = null, content, toaster) {
        this.$bvToast.toast(content, {
          title: `Variant ${variant || 'default'}`,
          variant: variant,
          toaster: toaster,
          solid: true
        })
      },
      onShown() {
        // Focus the cancel button when the overlay is showing
        this.$refs.cancel.focus()
      },
      onHidden() {
        // Focus the show button when the overlay is removed
        this.$refs.showProcessing.focus()
      },
  }
}
</script>